import { Pipe, PipeTransform } from '@angular/core';
import { Maybe } from '@sentinel/common/utils';

@Pipe({
  name: 'sentinelDefaultValueArray',
})
export class SentinelDefaultValueArrayPipe implements PipeTransform {
  transform<T>(value: Maybe<T[]>, defaultValue: T[] = []): T[] {
    return value ? value : defaultValue;
  }
}
