import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sentinelTooltipOnShortened',
})
export class SentinelTooltipOnShortenedPipe implements PipeTransform {
  transform(value: string | undefined | null, maxLength = 30): string {
    if (value === undefined || value === null) {
      return '';
    } else {
      return value.length > maxLength ? value : '';
    }
  }
}
