import { Pipe, PipeTransform } from '@angular/core';
import { isArray } from '@sentinel/common/utils';

interface FragmentNestedList<T> {
  data: T[];
}

@Pipe({
  name: 'sentinelHasElements',
})
export class SentinelHasElementsPipe implements PipeTransform {
  transform(value?: unknown[] | FragmentNestedList<unknown>): boolean {
    if (value === undefined || value === null) {
      return false;
    }
    if (isArray(value)) {
      return value.length > 0;
    } else {
      return value.data && value.data.length > 0;
    }
  }
}
