import { AsyncPipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';
import { Observable } from 'rxjs';

/**
 * Uses async pipe internally but retypes null to undefined as this is more in line with TypeScript optional attributes
 * and results in less retyping and boilerplate if strict checking is active. It also allows to provide default value
 * for cases where undefined type is not possible/desired.
 */
@Pipe({ name: 'sAsync', pure: false })
export class SentinelAsyncPipe extends AsyncPipe implements PipeTransform {
  transform<T>(obj: Observable<T> | Promise<T>): T | undefined;
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  transform<T>(obj: null | undefined): undefined;
  transform<T>(obj: Observable<T> | Promise<T> | null | undefined): T | undefined;
  transform<T>(obj: Observable<T> | Promise<T> | null | undefined, defaultValue?: T): T;

  transform<T>(obj: Observable<T> | Promise<T> | null | undefined, defaultValue?: T): T | undefined {
    const asyncPipeResult = super.transform(obj);
    if (asyncPipeResult !== undefined && asyncPipeResult !== null) {
      return asyncPipeResult;
    } else {
      if (defaultValue) {
        return defaultValue;
      } else {
        return undefined;
      }
    }
  }
}
